<template>
  <div class="cms-form-layout" style="padding-top:0" v-loading="loading">
    <el-form ref="form" :rules="rules" :model="formData" label-width="320px" style="margin-top:20px">
      <el-form-item label="是否开启店铺商品审核机制" prop="attrs.productCheck">
        <cms-switch activeValue='true' inactiveValue='false' v-model="formData.attrs.productCheck"></cms-switch>
        <div class="gray">全局设置商家商品是否需要进行平台审核上架。如设置否，依然可以通过店铺管理中对单独商家进行设置；反之，则忽略店铺设置</div>
      </el-form-item>
      <!-- <el-form-item label="是否开启店铺等级申请审核机制" prop="attrs.storeLevelCheck">
        <cms-switch activeValue='true' inactiveValue='false' v-model="formData.attrs.storeLevelCheck"></cms-switch>
        <div class="gray">全局设置是否允许商家申请升级店铺等级</div>
      </el-form-item> -->
        <el-form-item label="指定店铺结算时间" prop="attrs.storeSettlementN">
        <div>
          <span>每</span>
          <el-select v-model="formData.attrs.storeSettlementYT" style="width:140px" @change="changeDay">
            <el-option label="年" value="1"></el-option>
            <el-option label="月" value="2"></el-option>
          </el-select>
          <el-select v-model="formData.attrs.storeSettlementN" style="width:140px" 
           v-if="formData.attrs.storeSettlementYT==2">
            <el-option label="月末(自动识别月末时间)" value="0"></el-option>
            <el-option v-for="item in 31" :key="item" :label="item" :value="item"></el-option>
          </el-select>
          <el-select v-model="formData.attrs.storeSettlementN" style="width:140px" v-else>
            <el-option label="请选择" value=""></el-option>
            <el-option v-for="item in 12" :key="item" :label="item" :value="item"></el-option>
          </el-select>
        </div>
        <div class="gray">设置平台与店铺的结算时间，按年计算默认为该月第一天的凌晨5点，按月计算默认为该日的凌晨5点</div>
      </el-form-item>
      <!-- <el-form-item label="店铺等级升级申请未支付保留时效" prop="attrs.storeLevelUpUnpaymentTime">
        <el-input v-model="formData.attrs.storeLevelUpUnpaymentTime" maxlength="3">
           <template slot="append">天</template>
        </el-input>    
        <div class="gray">商家申请店铺等级信息，未支付的信息可保留期限，超过期限之后，系统将信息自动删除</div>
      </el-form-item> -->
      <el-form-item label="店铺保证金不足保留时效" prop="attrs.storeInsufficientBondPriceTime">
        <el-input v-model="formData.attrs.storeInsufficientBondPriceTime" maxlength="3">
           <template slot="append">天</template>
        </el-input>
        <div class="gray">设置店铺保证金余额低于规定保证金额度时保留商家启用状态时效，超出时效后，系统自动禁用帐号</div>
      </el-form-item>
      <el-form-item>
        <cms-button v-prem="'/shopSetting'" @click="saveData()" v-color>保存</cms-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import setting from './mixins.js'
export default {
  mixins: [form, setting],
  data() {
    return {
      rules: {
        'attrs.productCheck': [rules.required()],
        'attrs.storeLevelCheck': [rules.required()],
        'attrs.storeSettlementN': [rules.required('必须选择一个时间', 'change')],
        'attrs.storeLevelUpUnpaymentTime': [rules.required(), rules.number()],
        'attrs.storeInsufficientBondPriceTime': [rules.required(), rules.number()]
      }
    }
  },
  methods: {
    changeDay(value) {
      this.formData.attrs.storeSettlementN = ''
    }
  }
}
</script>




