// 商城设置相关接口
import axios from './../fetch'

// 全局设置信息
export const globalGet = params => { return axios.get('/admin/platform/config/global/get', { params: params }) }

// 全局设置信息编辑
export const globalSave = params => { return axios.post('/admin/platform/config/global/save', params) }

// 获取webapp下面的文件名称
export const getGlobalFolderList = params => { return axios.get('/admin/platform/config/global/folderList', { params: params }) }



