<template>
  <div class="cms-form-layout">
    <el-tabs v-model="activeName" @tab-click="changeTabs">
    <el-tab-pane label="系统信息" name="first">
      <system-info v-loading="loading"></system-info>
    </el-tab-pane>
    <el-tab-pane label="显示信息" name="second" lazy>
         <dispaly-info v-loading="loading"></dispaly-info>
    </el-tab-pane>
     <el-tab-pane label="会员注册" name="third" lazy>
         <member v-loading="loading"></member>
    </el-tab-pane>
    <el-tab-pane label="店铺设置" name="fourth" lazy>
        <store-set v-loading="loading"></store-set>
    </el-tab-pane>
     <el-tab-pane label="订单设置" name="six" lazy>
        <order-set v-loading="loading"></order-set>
    </el-tab-pane>
    <el-tab-pane label="商品检索" name="seven" lazy>
        <product-set v-loading="loading"></product-set>
    </el-tab-pane>
    <el-tab-pane label="扩展应用" name="five" lazy>
      <extend-app v-loading="loading"></extend-app>
    </el-tab-pane>
  </el-tabs>
  </div>
</template>

<script>
import systemInfo from './systemInfo'
import orderSet from './order'
import dispalyInfo from './dispalyInfo'
import extendApp from './extendApp'
import member from './member'
import storeSet from './storeSet'
import productSet from './productSet'
import { globalGet } from '@/api/setting/shopSetting.js'
export default {
  name: 'shopSetting',
  components: {
    systemInfo,
    orderSet,
    dispalyInfo,
    extendApp,
    member,
    storeSet,
    productSet
  },
  data() {
    return {
      activeName: 'first',
      loading: true
    }
  },
  methods: {
    getFormData() {
      globalGet().then(res => {
        const params = res.data
        params.uploadSuffixGroup = res.data.attrs.uploadSuffix.split(',')// 上传类型组
        this.$store.dispatch('setGlobalData', params).then(res => {
          this.loading = false
        })// 把获取到的数据存在vuex里
      })
    },
    changeTabs(name) {
      this.$store.commit('SET_GLOBAL_TAG', this.activeName)
    }
  },
  created() {
    if (this.$store.state.system.globalData.id) {
      this.loading = false
    } else {
      this.getFormData()
    }
    // this.activeName = this.$store.state.system.globalDataTag
  }

}
</script>

<style>

</style>



