<template>
  <div class="cms-form-layout" style="padding-top:0" v-loading="loading">
    <el-form ref="form" :rules="rules" :model="formData" label-width="320px" style="margin-top:20px" 
     class="register-form">
      <el-form-item label="收藏量权重" prop="attrs.collectionFactor">
        <el-input-number v-model="formData.attrs.collectionFactor" :max="10" :min="1" size="small"></el-input-number>
        <div class="gray">设置商品的收藏量在商品检索中所占的权重，在同等搜索结果中，值越大，优先排列在前</div>
      </el-form-item>
        <el-form-item label="销售量权重" prop="attrs.saleFactor">
        <el-input-number v-model="formData.attrs.saleFactor"  :max="10" :min="1" size="small"></el-input-number>
        <div class="gray">设置商品的销售量在商品检索中所占的权重，在同等搜索结果中，值越大，优先排列在前</div>
      </el-form-item>
        <el-form-item label="自营权重" prop="attrs.selfSupportFactor">
        <el-input-number v-model="formData.attrs.selfSupportFactor" :max="10" :min="1" size="small"></el-input-number>
        <div class="gray">设置商品是否为自营商品因素在商品检索中所占的权重，在同等搜索结果中，值越大，优先排列在前</div>
      </el-form-item>
        <el-form-item label="评分权重" prop="attrs.avgScoreFactor">
        <el-input-number v-model="formData.attrs.avgScoreFactor" :max="10" :min="1" size="small"></el-input-number>
        <div class="gray">设置商品的评分数在商品检索中所占的权重，在同等搜索结果中，值越大，优先排列在前</div>
      </el-form-item>
        <el-form-item label="新品权重" prop="attrs.shelfDayFactor">
        <el-input-number v-model="formData.attrs.shelfDayFactor" :max="10" :min="1" size="small"></el-input-number>
        <div class="gray">设置商品是否为新品因素在商品检索中所占的权重，在同等搜索结果中，值越大，优先排列在前</div>
      </el-form-item>
      <el-form-item>
        <cms-button v-prem="'/shopSetting'" @click="saveData()" v-color>保存</cms-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import setting from './mixins.js'
export default {
  mixins: [form, setting],
  data() {
    return {
      rules: {
        'attrs.collectionFactor': [rules.required(), rules.number()],
        'attrs.saleFactor': [rules.required(), rules.number()],
        'attrs.avgScoreFactor': [rules.required(), rules.number()],
        'attrs.selfSupportFactor': [rules.required(), rules.number()],
        'attrs.shelfDayFactor': [rules.required(), rules.number()]
      }
    }
  }

}
</script>




