<template>
  <div class="cms-form-layout" style="padding-top:0" v-loading="loading">
    <el-form ref="form" :rules="rules" :model="formData" label-width="320px" style="margin-top:20px" class="register-form">
      <el-form-item label="订单金额最高支持积分抵扣额度比例" prop="attrs.orderScoreOffset" ref="attrs.orderScoreOffset" >
        <el-input-number v-model="formData.attrs.orderScoreOffset" :precision="1" :step="0.1" :max="1" :min="0" size="small"></el-input-number>
        <div class="gray">填写0至1之间的数字，设置订单金额最高能使用积分抵扣额度的比例，如订单100.00，比例为0.1，则最高抵扣额度为10</div>
      </el-form-item>
      <el-form-item label="积分换算比例" prop="attrs.scoreConversion" ref="attrs.scoreConversion">
        <!-- <el-input v-model="formData.attrs.scoreConversion" class="sort-input" type="number"> </el-input> -->
         <el-input-number v-model="formData.attrs.scoreConversion" :step="1" :max="100" :min="1" size="small"></el-input-number>
        <div class="gray">比例填写整数，表示100积分换算多少购买金额</div>
      </el-form-item>
      <el-form-item label="积分最低使用金额限制" prop="attrs.minScoreForUseMoney"  ref="attrs.minScoreForUseMoney">
        <el-input v-model="formData.attrs.minScoreForUseMoney" class="sort-input" maxlength="8"></el-input>
        <div class="gray">设置订单金额必须高于设定金额，才能使用积分抵扣</div>
      </el-form-item>
      <el-form-item label="订单金额最高支持红包抵扣额度比例" prop="attrs.orderForUseRpk" ref="attrs.orderForUseRpk">
        <el-input-number v-model="formData.attrs.orderForUseRpk" :precision="1" :step="0.1" :max="1" :min="0" size="small"></el-input-number>
        <div class="gray">填写0至1之间数字，设置订单金额最高能使用红包抵扣额度的比例，如订单100.00，比例为0.1，则最高抵扣额度为10</div>
      </el-form-item>
      <el-form-item label="红包换算比例" prop="attrs.rpkConversion"  ref="attrs.rpkConversion">
        <!-- <el-input v-model="formData.attrs.rpkConversion" class="sort-input" type="number"></el-input> -->
        <el-input-number v-model="formData.attrs.rpkConversion" :step="1" :max="100" :min="1" size="small"></el-input-number>
        <div class="gray">比例填写整数，表示100红包换算多少购买金额</div>
      </el-form-item>
      <el-form-item label="红包最低使用金额限制" prop="attrs.minRpkRestrict" ref="attrs.minRpkRestrict">
        <el-input v-model="formData.attrs.minRpkRestrict" class="sort-input" type="number"></el-input>
        <div class="gray">填写整数，设置订单金额必须高于设定金额，才能使用红包抵扣</div>
      </el-form-item>
       <el-form-item label="订单自动确认收货时效" prop="attrs.orderAutoReceiveTime"  ref="attrs.orderAutoReceiveTime">
        <el-input v-model="formData.attrs.orderAutoReceiveTime" class="sort-input" maxlength="3">
          <template slot="append">天</template>
        </el-input>  
        <div class="gray">设置订单自动确认收货时效，以订单发货时间开始计算，超过期限，系统自动确认收货</div>
      </el-form-item>
       <el-form-item label="订单自动关闭时效" prop="attrs.orderCloseHour"  ref="attrs.orderCloseHour">
        <el-input v-model="formData.attrs.orderCloseHour" class="sort-input" maxlength="2">
          <template slot="append">小时</template>
        </el-input>  
        <div class="gray">设置订单在用户未支付的情况下，系统自动关闭时效</div>
      </el-form-item>
        <el-form-item label="延长订单收货时长" prop="attrs.orderExtendReceiveTime"  ref="attrs.orderExtendReceiveTime">
        <el-input v-model="formData.attrs.orderExtendReceiveTime"  class="sort-input" maxlength="2">
          <template slot="append">天</template>
        </el-input>  
        <div class="gray">设置用户延长收货操作时的天数</div>
      </el-form-item>
      <!-- error -->
        <el-form-item label="订单申请售后时效" prop="attrs.orderRefundReceiveDay"  ref="attrs.orderRefundReceiveDay">
        <el-input   v-model="formData.attrs.orderRefundReceiveDay" class="sort-input" maxlength="3">
          <template slot="append">天</template>
        </el-input>  
        <div class="gray">设置用户延长收货操作时的时长，以订单确认收货时间开始，超出该时长后，用户无法再对订单申请售后操作</div>
      </el-form-item> 
        <el-form-item label="售后等待卖家审核时效" prop="attrs.orderRefundSellerCheckDay" ref="attrs.orderRefundSellerCheckDay">
        <el-input v-model="formData.attrs.orderRefundSellerCheckDay"  class="sort-input"  maxlength="3">
          <template slot="append">天</template>
        </el-input>  
        <div class="gray">设置用户提交的售后申请，等待卖家审核时效，以用户申请服务单时间开始，超出时效后，系统会自动审核通过</div>
      </el-form-item> 
     <el-form-item label="售后等待买家发货时效" prop="attrs.orderRefundMemberShipDay" ref="attrs.orderRefundMemberShipDay">
        <el-input v-model="formData.attrs.orderRefundMemberShipDay" class="sort-input"  maxlength="3">
          <template slot="append">天</template>
        </el-input>  
        <div class="gray">设置卖家审核售后服务单(仅限退货/换货)后，以卖家审核通过时间开始，用户未发货，超出时效后，系统结束当前售后申请服务</div>
      </el-form-item> 
       <el-form-item label="售后等待卖家收货时效" prop="attrs.orderRefundSellerReceiveDay"  ref="attrs.orderRefundSellerReceiveDay">
        <el-input v-model="formData.attrs.orderRefundSellerReceiveDay"  class="sort-input"  maxlength="3">
          <template slot="append">天</template>
        </el-input>  
        <div class="gray">设置买家发货后(仅限退货/换货服务)后，以买家发货时间开始，卖家未确认收货，超出时效后，系统自动确认收货</div>
      </el-form-item> 
        <el-form-item label="售后等待卖家发货时效" prop="attrs.orderRefundSellerShipDay" ref="attrs.orderRefundSellerShipDay">
        <el-input v-model="formData.attrs.orderRefundSellerShipDay" class="sort-input"  maxlength="3">
          <template slot="append">天</template>
        </el-input>  
        <div class="gray">设置确认收货之后（仅限换货服务），以卖家确认收货时间开始，卖家未发货，超出时效后，系统自动发起退款处理，换货失败</div>
      </el-form-item> 
      <el-form-item label="售后等待买家收货时效" prop="attrs.orderRefundMemberReceiveDay" ref="attrs.orderRefundMemberReceiveDay">
        <el-input v-model="formData.attrs.orderRefundMemberReceiveDay" class="sort-input"  maxlength="3">
          <template slot="append">天</template>
        </el-input>  
        <div class="gray">设置卖家发货后(仅限换货服务)后，以卖家发货时间开始，用户未确认收货，超出时效后，系统自动确认收货</div>
      </el-form-item> 
      <el-form-item label="订单自动评价时效" prop="attrs.orderCommentMaxTime" ref="attrs.orderCommentMaxTime">
        <el-input v-model="formData.attrs.orderCommentMaxTime" class="sort-input"  maxlength="3">
          <template slot="append">天</template>
        </el-input>  
        <div class="gray">设置订单自动评价时效后，在订单确认收货成功后，超过设置天数后，系统会自动进行订单好评</div>
      </el-form-item> 
      <el-form-item>
        <cms-button v-prem="'/shopSetting'" @click="saveData()" v-color>保存</cms-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import setting from './mixins.js'
export default {
  mixins: [form, setting],
  data() {
    return {
      rules: {
        'attrs.orderScoreOffset': [rules.required(), rules.float()],
        'attrs.scoreConversion': [rules.required(), rules.number()],
        'attrs.minScoreForUseMoney': [rules.required(), rules.float(), rules.decimal()],
        'attrs.orderForUseRpk': [rules.required(), rules.float()],
        'attrs.rpkConversion': [rules.required(), rules.number()],
        'attrs.minRpkRestrict': [rules.required(), rules.number(), rules.max(8)],
        'attrs.orderAutoReceiveTime': [rules.required(), rules.number()],
        'attrs.orderCloseHour': [rules.required(), rules.number()],
        'attrs.orderExtendReceiveTime': [rules.required(), rules.number()],
        'attrs.orderRefundReceiveDay': [rules.required(), rules.number()],
        'attrs.orderRefundSellerCheckDay': [rules.required(), rules.number()],
        'attrs.orderRefundMemberShipDay': [rules.required(), rules.number()],
        'attrs.orderRefundSellerReceiveDay': [rules.required(), rules.number()],
        'attrs.orderRefundSellerShipDay': [rules.required(), rules.number()],
        'attrs.orderRefundMemberReceiveDay': [rules.required(), rules.number()],
        'attrs.orderCommentMaxTime': [rules.required(), rules.number()]
      }
    }
  }
}
</script>




