
<template>
  <div>
    <el-input style="width:380px;cursor: pointer;" v-model="checkedNames" :readonly="true">
      <el-button @click="initData" slot="append" icon="el-icon-search"></el-button>
    </el-input>
    <el-dialog title='会员选择' :visible="dialogVisible" width="900px" :before-close="close">
      <div class="cms-dialog__body">
      <section class="cms-list-query" style="margin-left:20px">
      <div class="query-group">
        <label>菜单名称</label>
        <el-input v-model="queryParams.name" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
      <el-table :data="tableData" style="cursor: pointer;" ref='dataTable'
      @selection-change='getCheckItems'
       @row-click='checkRows' max-height='500'>
        <!-- 多选 -->
        <el-table-column width="60"  type="selection" v-if="multiple">
        </el-table-column>
        <!-- 单选 -->
        <el-table-column width="60" align="center" v-else>
          <div slot-scope="scope">
            <el-radio :label="scope.$index" v-model="selectIndex" >&nbsp;</el-radio>
          </div>
        </el-table-column>
           <el-table-column label="菜单名称" min-width="240">
        <template slot-scope="scope">
          <div class="menu-name" :style="{marginLeft:(scope.row._level*16)+'px'}" v-if="iconShow(scope.$index,scope.row)" @click="toggleExpanded(scope.$index)">
            <span class="tree-ctrl">
              <i v-if="scope.row._expanded" class="el-icon-caret-right"></i>
              <i v-else class="el-icon-caret-bottom"></i>
            </span>
            <span>{{scope.row.name}}</span>
          </div>
          <div v-else :style="{marginLeft:(scope.row._level*16)+'px'}">
            <span class="icon-dot">○</span>
            <span>{{scope.row.name}}</span>
          </div>
        </template>
      </el-table-column>
      </el-table>
      <cms-pagination @change="getPage" :total='total'></cms-pagination>
      </div>
      <span slot="footer" class="dialog-footer">
        <cms-button type="primary" @click="determine">确 定</cms-button>
        <cms-button type='info' @click="close">取 消</cms-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import treeToArray from '@/utils/eval.js'
import {
  // artCategoryTree
  artCategoryGetChild
} from '@/api/article/articleCategory.js'
import list from '@/mixins/list'
export default {
  mixins: [list],
  name: 'cmsMember',
  props: {
    value: {},
    multiple: {
      type: Boolean,
      default: true
    },
    labelName: {
      type: String,
      default: 'name'
    }
  },
  data() {
    return {
      dialogVisible: false,
      checkItems: [],
      selectIndex: '', // 全选状态，默认全不选
      queryParams: {
        parentId: '',
        childs: {
          parentId: ''
        }
      },
      checkedNames: '', // 临时显示的名称
      typeList: [],
      tableData: [],
      expandAll: false,
      selfId: 0, // 当前点击的id 不可删除
      columnState: true // 判断行隐藏
    }
  },
  methods: {
    getCurrentRow(row) {

    },
    initData() {
      this.dialogVisible = true
      this.getTableData(artCategoryGetChild, this.queryParams)
    },
    checkRows(row) { // 单击行选中
      if (this.multiple) { // 多选时
        this.$refs.dataTable.toggleRowSelection(row)
      } else { // 单选时
        this.selectIndex = this.tableData.indexOf(row)
        this.checkItems = [row]
      }
    },
    determine() {
      this.$emit('get', this.checkItems)
      this.$emit('input', parseInt(this.checkItems.map(v => v.id)))
      this.checkedNames = this.checkItems.map(v => v[this.labelName]).toString()
      this.close()
    },
    close(done) {
      this.checkItems = []
      this.selectIndex = ''
      this.$refs.dataTable.clearSelection()
      this.dialogVisible = false
    },
    showRow: function(row) {
      const show = row.row._show
      row.row._show = show
      return show
        ? 'animation:treeTableShow 1s;-webkit-animation:treeTableShow 1s;'
        : 'display:none;'
    }, // 切换下级是否展开
    toggleExpanded: function(trIndex) {
      const record = this.tableData[trIndex]
      if (record._expanded) {
        // 判断展开
        if (!record._load) {
          // 判断是否加载过
          this.queryParams.parentId = record.id
          this.loading = true
          artCategoryGetChild(this.queryParams).then(res => {
            if (res.code === 200) {
              const arr = treeToArray(res.data)
              for (let i = 0; i < arr.length; i++) {
                this.tableData.splice(trIndex + (i + 1), 0, arr[i])
              }
              record._expanded = false
              record._load = true
              this.loading = false
            }
          })
        } else {
          this.chidView(record, true)
          record._expanded = false
        }
      } else {
        record._expanded = true
        this.chidView(record, false)
      }
    },
    chidView(record, state) {
      // 子集显示，核心判断这个方法
      const arr = []
      Array.from(this.tableData).forEach(v => {
        if (v.nodeIds.indexOf(record.id) > -1 && v.id !== record.id) {
          v._show = state
        }
        arr.push(v)
      })
      this.tableData = arr
    },
    // 图标显示
    iconShow(index, record) {
      return !record.isChild
    },
    getTableData(callBack, params) {
      callBack(params).then(res => {
        if (res.code === 200) {
          this.tableData = treeToArray(res.data)
          this.loading = false
        }
      })
    }
    // getTypeList() {
    //   this.getTableData(artCategoryGetChild, this.queryParams)
    // }
  }
}
</script>

<style lang='scss' scoped>
</style>



