<template>
  <div class="cms-form-layout" style="padding-top:0" v-loading="loading">
    <el-form
      ref="form"
      :rules="rules"
      :model="formData"
      label-width="210px"
      style="margin-top:20px"
      @submit.native.prevent
    >
      <el-form-item label="默认显示地区" prop="displayAreaId" ref="attrs_mallName">
        <el-select v-model="formData.displayAreaId" placeholder="请选择" :autofocus="true" ref="first">
          <el-option
            v-for="item in areaData"
            :key="item.areaCode"
            :label="item.areaName"
            :value="item.id"
          ></el-option>
        </el-select>
        <div class="gray"></div>
      </el-form-item>
      <el-form-item label="首页搜索关键词" prop="attrs.pHKeywords">
        <el-input v-model="formData.attrs.pHKeywords" maxlength="200"></el-input>
        <div class="gray">显示在商城页面title中</div>
      </el-form-item>
      <el-form-item label="热门搜索词" prop="attrs.hotsKeywords">
        <el-input v-model="formData.attrs.hotsKeywords" maxlength="200"></el-input>
        <div class="gray">显示在商城页面description中</div>
      </el-form-item>
      <el-form-item label="版权信息及ICP备案号" prop="attrs.icpRecordNumber">
        <el-input
          v-model="formData.attrs.icpRecordNumber"
          type="textarea"
          :rows="3"
          resize="none"
          maxlength="200"
        ></el-input>
        <div class="gray">信息将显示在商城最底部</div>
      </el-form-item>
      <el-form-item label="商城用户注册协议文章" prop="regisAgreementArtId">
        <!-- 修改过的组件 -->
        <cms-article-category
          v-model="formData.regisAgreementArtId"
          :labelData="formData.regisAgreementArt"
          labelName="title"
        ></cms-article-category>
        <div class="gray">显示在商城用户注册协议</div>
      </el-form-item>
      <el-form-item label="商家入驻协议文章" prop="enterAgreementArtId">
        <cms-article-category
          v-model="formData.enterAgreementArtId"
          :labelData.sync="formData.enterAgreementArt"
          labelName="title"
        ></cms-article-category>
        <div class="gray">显示在商城商家入驻时协议</div>
      </el-form-item>
      <el-form-item label="PC模板方案" prop="attrs.pcSolution">
        <el-select v-model="formData.attrs.pcSolution">
          <el-option v-for="item in folderItems" :key="item" :label="item" :value="item"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="WAP模板方案" prop="attrs.mobileSolution">
        <el-select v-model="formData.attrs.mobileSolution">
          <el-option v-for="item in folderItems" :key="item" :label="item" :value="item"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="平板模板方案" prop="attrs.tabletSolution">
        <el-select v-model="formData.attrs.tabletSolution">
          <el-option v-for="item in folderItems" :key="item" :label="item" :value="item"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="微信公众号二维码" prop="wechatPublicNumberImgId">
        <cms-picture-zone
          :data="formData.wechatPublicNumberImgResource"
          v-model="formData.wechatPublicNumberImgId"
        >
          <cms-button size="small" type="info" icon="correlation">上传图片</cms-button>
        </cms-picture-zone>
      </el-form-item>
      <el-form-item label="微信小程序二维码" prop="wechatSmallImgId">
        <cms-picture-zone
          :data="formData.wechatSmallImgResource"
          v-model="formData.wechatSmallImgId"
        >
          <cms-button size="small" type="info" icon="correlation">上传图片</cms-button>
        </cms-picture-zone>
      </el-form-item>
      <el-form-item label="商城LOGO图" prop="mallImgId">
        <cms-picture-zone :data="formData.mallImgResource" v-model="formData.mallImgId">
          <cms-button size="small" type="info" icon="correlation">选择图标</cms-button>
        </cms-picture-zone>
      </el-form-item>
      <el-form-item label="用户会员中心LOGO图" prop="operIdentityImgId">
        <cms-picture-zone
          :data="formData.operIdentityImgResource"
          v-model="formData.operIdentityImgId"
        >
          <cms-button size="small" type="info" icon="correlation">选择图标</cms-button>
        </cms-picture-zone>
      </el-form-item>
      <el-form-item label="平台端登录页LOGO图" prop="loginPlatformImgId">
        <cms-picture-zone
          :data="formData.loginPlatformImgResource"
          v-model="formData.loginPlatformImgId"
        >
          <cms-button size="small" type="info" icon="correlation">选择图标</cms-button>
        </cms-picture-zone>
      </el-form-item>

      <el-form-item label="平台端LOGO图" prop="platformImgId">
        <cms-picture-zone :data="formData.platformImgResource" v-model="formData.platformImgId">
          <cms-button size="small" type="info" icon="correlation">选择图标</cms-button>
        </cms-picture-zone>
      </el-form-item>
      <el-form-item label="商家端登录页LOGO图" prop="loginStoreImgId">
        <cms-picture-zone :data="formData.loginStoreImgResource" v-model="formData.loginStoreImgId">
          <cms-button size="small" type="info" icon="correlation">选择图标</cms-button>
        </cms-picture-zone>
      </el-form-item>
      <el-form-item label="商家端LOGO图" prop="storeImgId">
        <cms-picture-zone :data="formData.storeImgResource" v-model="formData.storeImgId">
          <cms-button size="small" type="info" icon="correlation">选择图标</cms-button>
        </cms-picture-zone>
      </el-form-item>
      <el-form-item>
        <cms-button v-prem="'/shopSetting'" @click="saveData()" v-color>保存</cms-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
// import rules from '@/rules'
import form from '@/mixins/form.js'
import { getGlobalFolderList } from '@/api/setting/shopSetting.js'
import cmsArticleTree from '@/components/cms/cmsArticleTree.vue'
import cmsArticleCategory from '@/components/cms/cmsArticleCategory.vue'
import { areaRegionList } from '@/api/setting/area.js'
import setting from './mixins.js'
export default {
  mixins: [form, setting],
  data() {
    return {
      rules: {},
      folderItems: [],
      queryParams: {
        parentId: '',
        areaType: '1',
        code: '',
        name: ''
      },
      areaData: []
    }
  },
  components: {
    cmsArticleTree,
    cmsArticleCategory
  },
  methods: {
    getGlobalFolderList() {
      getGlobalFolderList().then(res => {
        if (res.code === 200) {
          this.folderItems = res.data
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    getArea() {
      areaRegionList(this.queryParams).then(res => {
        if (res.code === 200) {
          this.areaData = res.data
        }
      })
    }
  },
  created() {
    this.getGlobalFolderList()
    this.getArea()
  }
}
</script>




