<template>
  <div class="cms-form-layout" style="padding-top:0" v-loading="loading"  >
    <el-form ref="form" :rules="rules" :model="formData" label-width="210px" style="margin-top:20px"
    >
      <el-form-item label="商城名称" prop="attrs.mallName"  ref="attrs.mallName" :error='errorMessage.smtpService'>
        <el-input v-model="formData.attrs.mallName" :autofocus="true" ref="first" maxlength="50"></el-input>
        <div class="gray"></div>
      </el-form-item>
      <el-form-item label="标题" prop="attrs.title" :error='errorMessage.smtpPort'>
        <el-input v-model="formData.attrs.title" maxlength="100"></el-input>
        <div class="gray">显示在商城页面title中</div>
      </el-form-item>
      <el-form-item label="描述" prop="attrs.description" :error='errorMessage.emailPassword'>
        <el-input v-model="formData.attrs.description" maxlength="200"></el-input>
        <div class="gray">显示在商城页面description中</div>
      </el-form-item>
      <el-form-item label="关键词" prop="attrs.keywords" :error="errorMessage.testemail">
        <el-input v-model="formData.attrs.keywords" maxlength="100"></el-input>
        <div class="gray">显示在商城页面keyworkds中</div>
      </el-form-item>
      <el-form-item label="ftp" prop="uploadFtpId" :error="errorMessage.testemail">
        <el-select v-model="formData.uploadFtpId">
          <el-option label="请选择" :value='null'></el-option>
          <el-option v-for="item in ftpItems" :key="item.id"
          :label="item.ftpName"
          :value="item.id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="云存储" prop="uploadOssId" :error="errorMessage.testemail">
       
        <el-select v-model="formData.uploadOssId">
          <el-option label="请选择" :value='null'></el-option>
          <el-option v-for="item in ossItems" :key="item.id"
          :label="item.ossName"
          :value="item.id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="上传模式" prop="attrs.uploadWay" :error="errorMessage.testemail">
        <el-radio-group v-model="formData.attrs.uploadWay">
            <el-radio label="1">先云存储后FTP</el-radio>
              <el-radio label="2">先FTP后云存储</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="附件上传最大限制" prop="attrs.uploadSize" :error="errorMessage.testemail">
        <el-select v-model="formData.attrs.uploadSize">
          <el-option v-for="item in sizeItems" :key="item.label" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="附件上传后缀" prop="uploadSuffixGroup" :error="errorMessage.testemail" >
        <el-select v-model="formData.uploadSuffixGroup" multiple filterable size="mini">
          <el-option-group v-for="group in typeItems" :key="group.label" :label="group.label">
            <el-option v-for="item in group.options" :key="item.value" :label="item.label" :value="item.value">
              {{item.label}}
            </el-option>
          </el-option-group>
        </el-select>
      </el-form-item>
      <el-form-item label="客服电话" prop="attrs.serverPhone" :error="errorMessage.testemail">
        <el-input v-model="formData.attrs.serverPhone" maxlength="15"></el-input>
      </el-form-item>
      <el-form-item label="客服邮箱" prop="attrs.serverEmail" :error="errorMessage.testemail">
        <el-input v-model="formData.attrs.serverEmail" maxlength="50"></el-input>
      </el-form-item>
      <el-form-item label="联系地址" prop="attrs.contactAddress" :error="errorMessage.testemail">
        <el-input v-model="formData.attrs.contactAddress" maxlength="100"></el-input>
      </el-form-item>
      <el-form-item label="平台客服" prop="attrs.serverPlatform" :error="errorMessage.testemail">
        <el-input v-model="formData.attrs.serverPlatform" type="textarea" :rows="4" maxlength="200"></el-input>
        <span class="gray">设置平台客服，多组客服换行填写，用户联系客服，将随机选中其中一组。格式为：客服一|123456789|旺旺（仅支持QQ及旺旺）</span>
      </el-form-item>
      <el-form-item>
        <cms-button v-prem="'/shopSetting'" @click="saveData()" v-color>保存</cms-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import { mediaSize, mediaType } from './data.js'
import rules from '@/rules'
import form from '@/mixins/form.js'
import { occPage } from '@/api/setting/occCloudStorage.js'
import { ftpList } from '@/api/setting/ftp.js'
import setting from './mixins.js'

export default {
  mixins: [form, setting],
  data() {
    var validatePhone = (rule, value, callback) => {
      const reg = /^((0\d{2,3}-\d{7,8})|(1{1}\d{10}))$/
      if (value === '') {
        callback()
      } else if (!reg.test(value)) {
        callback(new Error('客服电话格式不正确'))
      } else {
        callback()
      }
    }
    return {
      typeItems: Object.assign({}, mediaType), // 上传类型集合
      sizeItems: Object.assign({}, mediaSize), // 上传类型集合
      ftpItems: [],
      ossItems: [],
      rules: {
        'attrs.mallName': [rules.required()],
        'attrs.serverEmail': [rules.email()],
        'attrs.uploadWay': [rules.required()],
        'attrs.description': [rules.max(140)],
        'attrs.serverPhone': [rules.custom(validatePhone)]
      }
    }
  },
  methods: {
    getData() {
      ftpList({ page: 1, size: 99999 }).then(res => {
        this.ftpItems = res.data.content
      })
      occPage({ page: 1, size: 99999 }).then(res => {
        this.ossItems = res.data.content
      })
    }
  },
  created() {
    this.getData()
  }
}
</script>




