<template>
  <div class="cms-form-layout" style="padding-top:0" v-loading="loading">
    <!-- 会员注册 -->
    <el-form ref="form" :rules="rules" :model="formData" label-width="320px" style="margin-top:20px" class="register-form">
      <el-form-item label="会员注册赠送积分" prop="attrs.memberRegistPresentScore" ref="attrs.memberRegistPresentScore">
        <el-input-number v-model="formData.attrs.memberRegistPresentScore" :step="1" :max="99999" :min="0" size="small"></el-input-number>
        <div class="gray"></div>
      </el-form-item>
      <el-form-item label="会员注册赠送红包" prop="attrs.memberRegistPresentRpk" ref="attrs.memberRegistPresentRpk">
        <el-input-number v-model="formData.attrs.memberRegistPresentRpk"  :step="1" :max="9999" :min="0" size="small"></el-input-number>
        <div class="gray"></div>
      </el-form-item>
      <el-form-item label="会员注册赠送成长值" prop="attrs.memberRegistGrowth" ref="attrs.memberRegistGrowth">
        <el-input-number v-model="formData.attrs.memberRegistGrowth" :step="1" :max="99999" :min="0" size="small"></el-input-number>
        <div class="gray"></div>
      </el-form-item>
      <el-form-item label="每日首次登陆赠送成长值" prop="attrs.firstLoginPerdayGiftExp"  ref="attrs.firstLoginPerdayGiftExp">
        <el-input-number v-model="formData.attrs.firstLoginPerdayGiftExp" :step="1" :max="99999" :min="0" size="small"></el-input-number>
        <div class="gray"></div>
      </el-form-item>
      <el-form-item label="购物赠送成长值比例" prop="attrs.shoppedGiftExtRatio" ref="attrs.shoppedGiftExtRatio">
        <el-input v-model="formData.attrs.shoppedGiftExtRatio" class="sort-input" maxlength="5">
            <template slot="append">%</template>
        </el-input>
        <div class="gray">用户确认收货后按比例赠送成长值，如，成交金额为100，则赠送100 * 10% = 10成长值</div>
      </el-form-item>
      <el-form-item label="单笔订单赠送成长值上限" prop="attrs.singleOrderMaxGiftExp" ref="attrs.singleOrderMaxGiftExp">
        <el-input-number v-model="formData.attrs.singleOrderMaxGiftExp" :step="1" :max="99999" :min="0" size="small"></el-input-number>
        <div class="gray">用户确认收货单笔订单最多可获得成长值上限</div>
      </el-form-item>
      <el-form-item label="购物赠送积分比例" prop="attrs.shoppedGiftScoreRatio" ref="attrs.shoppedGiftScoreRatio">
        <el-input v-model="formData.attrs.shoppedGiftScoreRatio"  class="sort-input" maxlength="5">
           <template slot="append">%</template>
        </el-input>
        <div class="gray">用户确认收货后按比例赠送积分，如，成交金额为100，则赠送100 * 10% = 10积分</div>
      </el-form-item>
      <el-form-item label="单笔订单赠送积分上限" prop="attrs.singleOrderMaxGiftScore" ref="attrs.singleOrderMaxGiftScore">
        <el-input-number v-model="formData.attrs.singleOrderMaxGiftScore" :step="1" :max="99999" :min="0" size="small"></el-input-number>
        <div class="gray">用户确认收货单笔订单最多可获得积分上限</div>
      </el-form-item>
      <el-form-item label="购物赠送红包比例" prop="attrs.shoppedGiftRedEnvelopeRatio" ref="attrs.shoppedGiftRedEnvelopeRatio">
        <el-input v-model="formData.attrs.shoppedGiftRedEnvelopeRatio"  class="sort-input" maxlength="5">
            <template slot="append">%</template>
        </el-input>
        <div class="gray">用户确认收货后按比例赠送红包，如，成交金额为100，则赠送100 * 10% = 10红包</div>
      </el-form-item>
      <el-form-item label="单笔订单赠送红包上限" prop="attrs.singleOrderMaxGiftRedEnvelope" ref="attrs.singleOrderMaxGiftRedEnvelope">
        <el-input-number v-model="formData.attrs.singleOrderMaxGiftRedEnvelope" :step="1" :max="99999" :min="0" size="small"></el-input-number>
        <div class="gray">用户确认收货单笔订单最多可获得红包上限</div>
      </el-form-item>
       <el-form-item label="订单评价赠送成长值" prop="attrs.orderCommentGiftExp" ref="attrs.orderCommentGiftExp">
        <el-input-number v-model="formData.attrs.orderCommentGiftExp" :step="1" :max="99999" :min="0" size="small"></el-input-number>
        <div class="gray">用户评价单笔有效订单，获取的成长值</div>
      </el-form-item>
       <el-form-item label="订单评价赠送积分" prop="attrs.orderCommentGiftScore" ref="attrs.orderCommentGiftScore">
        <el-input-number v-model="formData.attrs.orderCommentGiftScore" :step="1" :max="99999" :min="0" size="small"></el-input-number>
        <div class="gray">用户评价单笔有效订单，赠送积分数</div>
      </el-form-item>
       <el-form-item label="订单评价赠送红包" prop="attrs.orderCommentGiftRedEnvelope" ref="attrs.orderCommentGiftRedEnvelope">
        <el-input-number v-model="formData.attrs.orderCommentGiftRedEnvelope" :step="1" :max="99999" :min="0" size="small"></el-input-number>
        <div class="gray">
          用户评价单笔有效订单，赠送红包数额
        </div>
      </el-form-item>
      <el-form-item>
        <cms-button v-prem="'/shopSetting'" @click="saveData()" v-color>保存</cms-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import setting from './mixins.js'
export default {
  mixins: [form, setting],
  data() {
    var validateMax = (rule, value, callback) => { // 验证比例的最大值
      if (parseFloat(value) > 100) {
        callback(new Error('最大值不能超过100'))
      } else {
        callback()
      }
    }
    return {
      rules: {
        'attrs.memberRegistPresentScore': [rules.required(), rules.number()],
        'attrs.memberRegistPresentRpk': [rules.required(), rules.number()],
        'attrs.memberRegistGrowth': [rules.required(), rules.number()],
        'attrs.firstLoginPerdayGiftExp': [rules.required(), rules.number()],
        'attrs.shoppedGiftExtRatio': [rules.required(), rules.float(), rules.decimal(), rules.custom(validateMax)],
        'attrs.singleOrderMaxGiftExp': [rules.required(), rules.number()],
        'attrs.shoppedGiftScoreRatio': [rules.required(), rules.float(), rules.decimal(), rules.custom(validateMax)],
        'attrs.singleOrderMaxGiftScore': [rules.required(), rules.number()],
        'attrs.shoppedGiftRedEnvelopeRatio': [rules.required(), rules.float(), rules.decimal(), rules.custom(validateMax)],
        'attrs.singleOrderMaxGiftRedEnvelope': [rules.required(), rules.number()],
        'attrs.orderCommentGiftExp': [rules.required(), rules.number()],
        'attrs.orderCommentGiftScore': [rules.required(), rules.number()],
        'attrs.orderCommentGiftRedEnvelope': [rules.required(), rules.number()]
      }
    }
  }

}
</script>






