
<template>
  <div>
    <el-input style="width:380px;cursor: pointer;" v-model="checkedNames" :readonly="true">
      <el-button @click="initData" slot="append" icon="el-icon-search"></el-button>
    </el-input>
    <el-dialog title='文章列表' :visible="dialogVisible" width="900px" :before-close="close">
      <div class="cms-dialog__body">
      <section class="cms-list-query" >
        <div class="query-group">
          <label>文章分类</label>
          <el-select @change="query" v-model="queryParams.cateId" style="width:160px;">
            <el-option label="全部" value=""></el-option>
            <el-option v-for="item in artCateList" :key="item.id"
             :label="item.name" :value="item.id">
            </el-option>
          </el-select>
        </div>
        <div class="query-group">
          <label>文章标题</label>
          <el-input v-model="queryParams.title" @keyup.native.enter="query"></el-input>
        </div>
        <div class="query-group">
          <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
        </div>
      </section>
      <el-table :data="tableData" style="cursor: pointer;" ref='dataTable'
      @selection-change='getCheckItems'
       @row-click='checkRows' max-height='500'>
        <!-- 多选 -->
        <el-table-column width="60"  type="selection" v-if="multiple">
        </el-table-column>
        <!-- 单选 -->
        <el-table-column width="60" align="center" v-else>
          <div slot-scope="scope">
            <el-radio :label="scope.$index" v-model="selectIndex" >&nbsp;</el-radio>
          </div>
        </el-table-column>
        <el-table-column prop="id" label="id" width="60"></el-table-column>
        <el-table-column prop="title" label="标题" min-width='120'></el-table-column>
        <el-table-column prop="category.name" label="分类名称" min-width="160"></el-table-column>
      </el-table>
      <cms-pagination @change="getPage" :total='total'></cms-pagination>
      </div>
      <span slot="footer" class="dialog-footer">
        <cms-button type="primary" @click="determine">确 定</cms-button>
        <cms-button type='info' @click="close">取 消</cms-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { artManagerList, artCategory } from '@/api/article/articleManager.js'
import list from '@/mixins/list'
export default {
  mixins: [list],
  name: 'cmsArticleCategory',
  props: {
    value: {},
    multiple: {
      type: Boolean,
      default: false
    },
    labelName: {
      type: String,
      default: 'name'
    },
    labelData: {}
  },
  data() {
    return {
      dialogVisible: false,
      checkItems: [],
      selectIndex: '', // 全选状态，默认全不选
      queryParams: {
        cateId: '',
        title: '',
        size: 10,
        page: 1 },
      checkedNames: '' // 临时显示的名称
    }
  },
  watch: {
    labelData: {
      handler() {
        if (this.multiple) {
          const name = this.labelData.map(v => v[this.labelName]).toString()
          this.checkedNames = name
        } else {
          const name = this.labelData[this.labelName]
          this.checkedNames = name
        }
      },
      immediate: true// 激活后立即触发一次
    }
  },

  methods: {
    initData() {
      this.dialogVisible = true
      this.getTableData(artManagerList, this.queryParams)
      this.getArtCate()
    },
    checkRows(row) { // 单击行选中
      if (this.multiple) { // 多选时
        this.$refs.dataTable.toggleRowSelection(row)
      } else { // 单选时
        this.selectIndex = this.tableData.indexOf(row)
        this.checkItems = [row]
      }
    },
    getArtCate() {
      // 获取文章分类
      artCategory({ parentId: '' }).then(res => {
        if (res.code === 200) {
          this.artCateList = res.data
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    determine() {
      this.$emit('get', this.multiple ? this.checkItems : this.checkItems[0])
      this.$emit('update:labelData', this.multiple ? this.checkItems : this.checkItems[0])
      this.$emit('input', this.checkItems.map(v => v.id).toString())
      this.checkedNames = this.checkItems.map(v => v[this.labelName]).toString()
      this.close()
    },
    close(done) {
      this.checkItems = []
      this.selectIndex = ''
      this.$refs.dataTable.clearSelection()
      this.dialogVisible = false
    }
  }
}
</script>

<style>
</style>



