<template>
  <div class="cms-form-layout" style="padding-top:0" v-loading="loading">
    <el-form ref="form" :rules="rules" :model="formData" label-width="210px" style="margin-top:20px">
      <!-- <el-form-item label="商家自定义微信公众号模式" prop="attrs.storeWechatPublicNumberPattern" :error="errorMessage.testemail">
        <el-radio-group v-model="formData.attrs.storeWechatPublicNumberPattern">
          <el-radio label="1">不开启</el-radio>
          <el-radio label="2">全部开启</el-radio>
          <el-radio label="3">部分开启</el-radio>
        </el-radio-group>
      </el-form-item> -->
      <el-form-item label="快递鸟商户ID" prop="attrs.kdniaoBussinessId" ref="kdniaoBussinessKey" :error='errorMessage.smtpService'>
        <el-input v-model="formData.attrs.kdniaoBussinessId" :autofocus="true" maxlength="50"></el-input>
        <div class="gray">点击获取
          <a style="color: #208EE3;" href="http://www.kdniao.com/reg">快递鸟商户ID</a>，免费版仅支持日调用3000次，如需查询业务过多，请购买相应套餐</div>
      </el-form-item>
       <el-form-item label="快递鸟商户KEY" prop="attrs.kdniaoBussinessKey" ref="attrs_kdniaoBussinessKey" :error='errorMessage.smtpService'>
        <el-input v-model="formData.attrs.kdniaoBussinessKey" :autofocus="true" maxlength="100"></el-input>
      </el-form-item>
      <!-- <el-form-item label="腾讯地图密钥" prop="attrs.tencentmapKey" :error='errorMessage.tencentmapKey'>
        <el-input v-model="formData.attrs.tencentmapKey" maxlength="100"></el-input>
        <div class="gray">用于系统定位地理位置，点击获取
          <a href="http://lbs.qq.com/console/user_info.html">腾讯地图密钥</a>
        </div>
      </el-form-item> -->
      <!-- <el-form-item label="统计代码" prop="attrs.statisticalCode" :error="errorMessage.statisticalCode">
        <el-input v-model="formData.attrs.statisticalCode" type="textarea" :rows="4" maxlength="200"></el-input>
        <span class="gray">设置统计站点服务商嵌入代码，系统会将代码嵌入到前台每个页面</span>
      </el-form-item> -->
      <el-form-item>
        <cms-button v-prem="'/shopSetting'" @click="saveData()" v-color>保存</cms-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import setting from './mixins.js'
export default {
  mixins: [form, setting],
  data() {
    return {
      rules: {
        'attrs.storeWechatPublicNumberPattern': [rules.required()]
      }
    }
  }
}
</script>





