import { globalSave } from '@/api/setting/shopSetting.js'
export default {
  data() {
    return {
      loading: false
    }
  },
  computed: {
    formData() {
      return this.$store.state.system.globalData
    }
  },
  methods: {
    saveData() {
      this.$refs.form.validate((valid, item) => {
        if (valid) {
          const params = this.$cms.deepCopy(this.formData)
          delete params.configAttr
          params.attrs.uploadSuffix = params.uploadSuffixGroup.join(',')// 转换下类型
          this.loading = true
          globalSave(params).then(res => {
            this.loading = false
            if (res.code === 200) {
              this.$store.dispatch('setGlobalData', params)
              this.$successMessage('修改成功')
            } else {
              this.$errorMessage(res.message)
            }
          })
        } else {
          // 定位到错误的那一行
          let name = ''
          for (const key in item) {
            if (item.hasOwnProperty(key)) {
              name = key
              break
            }
          }
          this.errorPosition(name)
        }
      })
    }
  },
  created() {

  },
  mounted() {

  }
}



